import React, { useState, useEffect } from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { useRef } from "react";

const data = [
  {
    name: "HTML",
    level: 90,
  },
  {
    name: "CSS3",
    level: 60,
  },
  {
    name: "JS",
    level: 90,
  },
  {
    name: "Express.js",
    level: 95,
  },
  {
    name: "React.js",
    level: 80,
  },
  {
    name: "Redux.js",
    level: 90,
  },
];

const Skills = () => {
  const containerWrapper = useRef(null);

  const [chartWidth, setChartWidth] = useState(0);
  useEffect(() => {
    setChartWidth(containerWrapper.current.offsetWidth);
  }, []);
  return (
    <div className=" container h-100 d-flex align-items-center pt-3">
      <div className="container " ref={containerWrapper}>
        <h4 className="text-light text-center m-0 p-0">Skills</h4>
        <hr className="bg-white" />
        <div className="d-flex align-items-center  justify-content-center">
          <BarChart
            width={chartWidth}
            height={400}
            data={data}
            margin={{
              top: 20,
              right: 30,
              left: 0,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis className="text-light" dataKey="name" />
            <YAxis className="text-light" />
            <Tooltip/>
            <Legend className="text-light" />
            <Bar dataKey="level" stackId="a" fill="#6c757d" />
          </BarChart>
        </div>
      </div>
    </div>
  );
};

export default Skills;
